.component-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 0px;
    padding:40px;
}

.component-content{
    display: flex;
    align-items: center;
    column-gap: 9px;
    justify-content: flex-start;
}

.index-title{
    white-space: nowrap;
}

.image-content{
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image-container{
    display: flex;
    align-items: center;
    padding-left: 5px;
    column-gap: 8px;
    margin-bottom: 0px;
    margin-top: 5px;
}

.item-img {
    transition: 200ms all;
  }

.item-img:hover {
    transform: scale(1.5);
  }